.container {
  max-width: 600px;
  text-align: center;
  margin: 0 auto;
  padding: 20px;
}

label {
  margin-bottom: 10px;
  display: block;
}

select {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 10px;
}

.lds-dual-ring {
  width: 80px;
  height: 80px;
  border: 6px solid #ccc;
  border-color: #ccc rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: 20px auto;
  animation: lds-dual-ring 1.2s linear infinite;
  display: none;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.notiflix-error {
  color: red;
  margin-bottom: 20px;
  display: none;
}

#cat-info img {
  max-width: 100%;
  margin-bottom: 10px;
}

#cat-info h3 {
  margin-bottom: 5px;
}

#cat-info p {
  margin-bottom: 10px;
}

